@import url('https://fonts.googleapis.com/css2?family=Saira+Stencil+One&display=swap');

.home{
    color: aliceblue;
    z-index: -1;
    width: 100vm;
    height: 55vw;
    row-gap: 10vw;
    overflow: hidden;
}

.home .home-content .para-div{
    width: 100%;
    align-items: center;
    position: relative;
    top: -15vw;
    overflow: visible;
    white-space: nowrap;    
    display: flex;    
    flex-wrap: nowrap;
}

.home .home-content .name{
    color:#ffffff94;
    background: transparent;
    font-size: 8vw;
    font-family: "Saira Stencil One", cursive;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
    white-space: nowrap;    
    display: flex;    
    flex-wrap: nowrap;
}

.home .home-content .name span{
    display: flex;
    flex-direction: row;
}

.home .home-content .name span span{
    color:rgb(166, 255, 0);
    width: 100%;
}

.home .home-content .developer-tag{
    width: 100%;
    background: transparent;
    font-family: monospace , fantasy , cursive;
    font-weight: bolder;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: absolute;
    left: -13vw;
    top: 8vw;
}

.home .home-content .developer-tag #tag0{
    background: transparent;
    color: rgba(255, 255, 255, 0.1);
    position: relative;
    font-size: 4vw;
    height: 2.6vw;
}

.home .home-content .developer-tag #tag1{
    background: transparent;
    color: rgba(255, 255, 255, 0.2);
    position: relative;
    font-size: 5.5vw;
    height: 3.7vw;
}

.home .home-content .developer-tag #tag2{
    background: transparent;
    color: rgba(255, 255, 255, 0.35);
    position: relative;
    font-size: 7vw;
    height: 4.7vw;
}

.home .home-content .developer-tag #tag3{
    background: transparent;
    color: rgb(255, 255, 255);
    position: relative;
    font-size: 9vw;
}

.home .home-content img{
    position: relative;
    top: 3vw;
    left:  68vw;
    height: 50vw;
    width: auto;
}

@media (max-width: 821px){
    .home{
        height:300px;
        row-gap: 5vw;
    }

    .home .home-content .para-div{
        top: -20vw;
    }

    .home .home-content .name{
        font-size: 10vw;
        top: 0;
        color: aliceblue;
    }

    .home .home-content .developer-tag{
        left: -10vw;
        top: 10vw;
    }

    .home .home-content .developer-tag #tag0{
        font-size: 5vw;
        height: 3.3vw;
    }

    .home .home-content .developer-tag #tag1{
        font-size: 6.5vw;
        height: 4.3vw;
    }

    .home .home-content .developer-tag #tag2{
        font-size: 8vw;
        height: 5.3vw;
    }

    .home .home-content .developer-tag #tag3{
        font-size: 10vw;
    }

    .home .home-content img{
        top: 5vw;
        left: 60vw;
        height: 40vw;
    }
}

@media (max-width: 431px) {
    .home {
        height: 920px;
        row-gap: 20px;
    }

    .home .home-content .para-div {
        top: -250px;
        height: 66px;
        flex-direction: column;
        align-items: center;
        white-space: normal;
        background-color: rgb(0, 6, 20);
    }

    .home .home-content .name {
        font-size: 80px;  
        top: -30px;
        z-index: 1;
    }

    .home .home-content .developer-tag {
        left: 0px;
        top: 60vw;
        align-items: center;
        z-index: -1;
        width: 100vw;
        overflow: hidden;
    }

    .home .home-content .developer-tag #tag0 {
        font-size: 40px;
        height: 26px;
    }

    .home .home-content .developer-tag #tag1 {
        font-size: 50px;
        height: 36px;
    }

    .home .home-content .developer-tag #tag2 {
        font-size: 60px;
        height: 46px;
    }

    .home .home-content .developer-tag #tag3 {
        font-size: 70px;
        height: auto;
    }

    .home .home-content img {
        top: 5vw;
        left: 55vw;
        height: auto;
        width: 100%;
    }
}
