footer{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: rgba(240, 248, 255, 0.815);
    background-color: rgb(13, 9, 29);
    position: relative;
    top: 30vw;
    row-gap: 2vw;
    padding-top: 1vw;
    padding-bottom: 2vw;
}

footer h3{
    color: rgba(255, 255, 255, 0.733);
    background: transparent;
}

footer .social-links{
    display: flex;
    flex-direction: row;
    justify-content: center;
    column-gap: 2vw;
    background: transparent;
}

footer a svg{
    fill: aliceblue;
    height: 3vw;
    width: auto;
}

footer a svg:hover, footer a svg:active{
    fill: rgb(160, 255, 0);
}

footer p{
    background: transparent;
}

@media (max-width: 431px){
    footer{
        padding-bottom: 15px;
        height: 120px;
        top: 200px;
    }

    footer h3{
        padding-bottom: 10px;
    }

    footer a svg{
        height: 39px;
    }

    footer .social-links{
        padding-bottom: 10px;
    }
}